<template>
  <a-layout style="height: 100vh;">

    <!-- 左侧菜单 -->
    <a-layout-sider :resize-directions="['right']"
      style="
        max-width: 95%;
        width: auto; /* NOTE 2023-12-14: 使侧栏宽度能恰好容纳子元素内容
        在展开、收起菜单时动态变化宽度，使其能完全展示各选项内容 */
      ">

      <a-menu @menu-item-click="onClickMenuItem" :selected-keys="this.$route.fullPath.split('?').at(0)">
        <a-sub-menu key="account"><template #title><icon-user />账号管理</template>
          <a-sub-menu key="admin"><template #title><icon-robot />管理员账号管理</template>
            <a-menu-item key="/admin/account/admin/register"><template #icon><icon-robot-add /></template>创建管理员账号</a-menu-item>
          </a-sub-menu>

          <a-sub-menu key="teacher"><template #title><icon-book />教师账号管理</template>
            <a-menu-item key="/admin/account/teacher/register"><template #icon><icon-user-add /></template>创建教师账号</a-menu-item>
          </a-sub-menu>

          <a-sub-menu><template #title><icon-pen />学生账号管理</template>
            <a-menu-item key="/admin/account/student/register"><template #icon><icon-user-add /></template>创建学生账号</a-menu-item>
          </a-sub-menu>

        </a-sub-menu>

        <a-sub-menu key="course"><template #title><icon-calendar />课程管理</template>
          <a-sub-menu><template #title><icon-file />开课申请</template>
            <a-menu-item key="/admin/course/pending_course_list"><template #icon><icon-bookmark /></template>待审批的申请</a-menu-item>
            <a-menu-item key="/admin/course/not_pending_course_list"><template #icon><icon-select-all /></template>已审批的申请</a-menu-item>
          </a-sub-menu>

          <a-menu-item key="/admin/course/info"><template #icon><icon-find-replace /></template>课程详情</a-menu-item>
          <a-menu-item key="c_2" disabled><template #icon><icon-upload /></template>导入课程</a-menu-item>
        </a-sub-menu>

        <a-sub-menu key="permission"><template #title><icon-lock />权限管理</template>
          <a-menu-item key="p_1" disabled>开放学生选课权限</a-menu-item>
          <a-menu-item key="p_2" disabled>开放教师开课权限</a-menu-item>
          <a-menu-item key="p_2" disabled>开放教师打分权限</a-menu-item>
        </a-sub-menu>
      </a-menu>
      
    </a-layout-sider>

    <!-- 右侧路由 -->
    <a-layout-content>
      <router-view></router-view>
    </a-layout-content>

  </a-layout>
</template>

<script>
export default {

  data() {
    return {
    }
  },
  mounted() {
    this.user_id = this.$route.query.user_id
  },
  methods: {
    onClickMenuItem(key) {

      let query_data = {
        user_id: this.user_id
      }

      switch(key) {
        /* account */
        case "/admin/account/admin/register": {
          this.$router.push({ path: "/admin/account/admin/register", query: query_data })
          break
        }
        case "/admin/account/student/register": {
          this.$router.push({ path: "/admin/account/student/register", query: query_data })
          break
        }
        case "/admin/account/teacher/register": {
          this.$router.push({ path: "/admin/account/teacher/register", query: query_data })
          break
        }

        /* course */
        case "/admin/course/pending_course_list": {
          this.$router.push({ path: "/admin/course/pending_course_list", query: query_data })
          break
        }
        case "/admin/course/not_pending_course_list": {
          this.$router.push({ path: "/admin/course/not_pending_course_list", query: query_data })
          break
        }
        case "/admin/course/info": {
          this.$router.push({ path: "/admin/course/info", query: query_data })
          break
        }
        
        /* default */
        default: {
          this.$message.info(key + " 功能未开放")
        }
      }
    }
  }
}
</script>

<style scoped>

</style>